<template>
  <div>
    <Header />
    <Content />
    <Footer />
  </div>
</template>

<script>
import Content from "@/components/Content";
import Header from "@/components/Header";
import Footer from "@/components/Footer";
export default {
  name: "App",
  components: {
    Content,
    Header,
    Footer
  }
};
</script>
<style>

body, .navbar {
    background-color: rgb(246, 243, 248) !important;
};

#signup-btn {
    background-color: rgb(230, 232, 234);
    width: 150px;
};

#nav-btn1 {
    background-color: rgb(206, 207, 215);
    margin: 3px;
}

#nav-btn2 {
    background-color: rgb(226, 225, 230);
    margin: 3px;
}

.intro {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 56px;
    color: rgb(51,53,85);
}

.start-btn {
    background-color: rgb(114, 115, 141);
}

.go-down-button{
    margin: 0 auto;
    display:block;
    background-color: rgb(246, 243, 248);
    border: none;
}

.card-content {
    border: 10px none none none;
}

.text {
    width: 40%;
    border-top: none;
    border-right: none;
    border-left: none;
    border-color: rgb(226, 225, 231);
    border-width: thin;
    background-color: rgb(246, 243, 248);
    padding-bottom: 19px;
    margin-right: 30px;

}

.content {
    overflow: auto;
    max-width: 60ch;
  }

.text::-webkit-input-placeholder{
    font-size: 20px;
    color: rgb(194, 193, 201);

}

.text:focus {

}

.form {
    padding-top: 20px;
}


.info {
    padding-top: 50px;
    padding-bottom: 30px;
    color: rgb(116, 118, 139);
}

.intro-img {
    border-radius: 4px;
}

.intro-img:hover {
    box-shadow: 10px 10px 5px gray;
    transition: box-shadow 0.5s ease-in-out;

}

.navbar-item {
    padding-left: 30px;
    padding-right: 30px;
}

.separator {
    display: inline-block;
    width:8%;
    height: 2px;
    padding-top : 6px;
    margin-right: 2%;
    margin-left: 2%;
    border-top: 1px solid rgb(229, 228, 233);
}

.title {
    font-size: 45px;
}


.second{
    font-size: large;
}

.offer:hover{
    box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2);
    transition: box-shadow 1s;
    /* transition-timing-function: ease; */
    transition-property: transform;
    transition-duration: 1s;
    transform: translateY(-20px);
    border-top: 2px solid black;
}

button[type="submit"] {
    margin-left: -60px;
    height: 45px;
    width: 50px;
    background: rgb(114, 115, 141);
    border: 0;
    border-radius: 50px;

}

*{
    font-family: Arial, Helvetica, sans-serif;
}
</style>



